import React from 'react';
import logo from './8ball.svg';
import responses from './responses.json';
import './Magic8Ball.css';

class Magic8Ball extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      prophecy: "Think of a question and click the 8 Ball to be enlightened."
    }
  }
  
  prognosticate() {
    let index = Math.floor(Math.random() * 20);
    this.setState({
      prophecy: responses[index]
    })
  }

  render() {
    return (
      <div className="Magic8Ball">
        <header className="Magic8Ball-header">
          <img src={logo} className="Magic8Ball-logo" alt="logo" onClick={() => this.prognosticate()} />
          <p>
            {this.state.prophecy}
          </p>
        </header>
      </div>
    );  
  }
}

export default Magic8Ball;
